import s from "./List.module.scss";

import Loading from "./Loading";

const List = ({ items, children }) => {
  if (items.state === "loading") return <Loading />;
  if (items.state === "error") {
    const {
      response: { statusText, status },
    } = items.msg;
    return (
      <>
        <h3>
          Error: {status} {statusText}
        </h3>
        <p>Check network dev tools</p>
      </>
    );
  }
  if (items.values.length === 0) return <p>Looks empty...</p>;
  return (
    <div className={s.list}>{items.values.map((item) => children(item))}</div>
  );
};

export default List;
