import { useReducer } from "react";

const actionTypes = {
  fetch: "FETCH",
  set: "SET",
  err: "ERROR",
};

function infoReducer(state, action) {
  switch (action.type) {
    case actionTypes.fetch: {
      return { state: "loading", values: state.values };
    }
    case actionTypes.set: {
      return { state: "success", values: action.payload };
    }
    case actionTypes.add: {
      return { state: "success", values: [...state.values, action.payload] };
    }
    case actionTypes.err: {
      return { state: "error", values: state.values, msg: action.err };
    }
    default: {
      throw new Error(`Unhandled type: ${action.type}`);
    }
  }
}

function useInfo() {
  const [list, dispatch] = useReducer(infoReducer, {
    state: "loading",
    values: [],
  });

  const onFetch = () => dispatch({ type: actionTypes.fetch });
  const onError = (err) => dispatch({ type: actionTypes.err, err });
  const success = (payload) => {
    if (Array.isArray(payload)) dispatch({ type: actionTypes.set, payload });
    else dispatch({ type: actionTypes.add, payload });
  };

  return { list, onFetch, onError, success };
}

export default useInfo;
