import { Switch, Route, Redirect } from "react-router-dom";

import {
  search,
  fetchFeed,
  fetchUsers,
  fetchUserFeed,
  findSimilar,
} from "../../api";
import useInfo from "../../hooks/useInfo";

import Feed from "../Feed";
import Users from "../Users";
import Detail from "../Detail";
import Header from "../Header";
import Form from "../../components/Form";
import Edit from "../../components/Edit";

import s from "./App.module.scss";

function App() {
  const { list, onFetch, onError, success } = useInfo();

  const request = (fn) =>
    async function (...args) {
      onFetch();
      try {
        const result = await fn(...args);
        success(result);
      } catch (err) {
        onError(err);
      }
    };

  return (
    <div className={s.app}>
      <Header />
      <section className={s.content}>
        <Switch>
          <Route path="/wiki/:urlTitle">
            <Detail />
          </Route>
          <Route path="/wiki">
            <Feed title="All Posts" list={list} getFeed={request(fetchFeed)} />
          </Route>
          <Route path="/users/:arg">
            <Feed
              title="User Posts"
              list={list}
              getFeed={request(fetchUserFeed)}
            />
          </Route>
          <Route path="/users">
            <Users
              title="All Users"
              list={list}
              getUsers={request(fetchUsers)}
            />
          </Route>
          <Route path="/add">
            <Form title="ADD A NEW POST" mode="add" />
          </Route>
          <Route path="/edit/:urlTitle">
            <Edit title="EDIT POST" mode="edit" />
          </Route>
          <Route path="/search/:arg">
            <Feed title="Search By tag" list={list} getFeed={request(search)} />
          </Route>
          <Route path="/similar/:arg">
            <Feed
              title="Similar Pages"
              list={list}
              getFeed={request(findSimilar)}
            />
          </Route>
          <Redirect to="/wiki" />
        </Switch>
      </section>
    </div>
  );
}

export default App;
