import { useState } from "react";
import plus from "../assets/plus.svg";
import { useHistory } from "react-router-dom";

import { createPage } from "../api";

import s from "./Form.module.scss";

const initialState = {
  name: "",
  email: "",
  title: "",
  content: "",
  tags: "",
};

const Form = () => {
  const { push } = useHistory();
  const [formState, setForm] = useState(initialState);
  const [error, setError] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const newPage = await createPage(formState);
      push(`/wiki/${newPage.urlTitle}`);
    } catch ({ response }) {
      setError(response.data);
    }
  };

  const handleChange = ({ target }) => {
    const { value, name } = target;
    setForm({ ...formState, [name]: value });
  };

  return (
    <>
      <div className={s.container}>
        <form onSubmit={handleSubmit}>
          <div className={s.header}>
            <h2>ADD A NEW PAGE</h2>
            <img src={plus} onClick={handleSubmit} alt="add icon" width="25" />
          </div>
          <label>
            Author Name
            <input
              onChange={handleChange}
              name="name"
              type="text"
              value={formState.name}
              placeholder="Nombre completo del autor"
            />
          </label>
          <label>
            Author Email
            <input
              onChange={handleChange}
              name="email"
              type="email"
              value={formState.email}
              placeholder="Email del autor"
            />
          </label>
          <label>
            Page Title
            <input
              onChange={handleChange}
              name="title"
              type="text"
              value={formState.title}
              placeholder="Titulo del posteo"
            />
          </label>
          <label>
            Page Content
            <textarea
              onChange={handleChange}
              name="content"
              type="text"
              value={formState.content}
              placeholder="Contenido del post, incluye markdown"
            />
          </label>
          <label>
            Tags
            <input
              onChange={handleChange}
              name="tags"
              type="text"
              value={formState.tags}
              placeholder="Separar con comas (,)"
            />
          </label>
        </form>
      </div>
      {error && <p className={s.error}>{error}</p>}
    </>
  );
};

export default Form;
