import { useState, useEffect } from "react";
import { FaEdit } from "react-icons/fa";
import { useHistory, useRouteMatch } from "react-router-dom";

import { editPage, fetchPages } from "../api";

import s from "./Form.module.scss";

const initialState = {
  title: "",
  content: "",
  tags: "",
};

const Form = () => {
  const { push } = useHistory();
  const { params } = useRouteMatch();

  const [page, setPage] = useState(initialState);
  const [error, setError] = useState("");

  useEffect(() => {
    fetchPages(params.urlTitle)
      .then((page) => {
        setPage(page);
      })
      .catch(({ response }) => {
        setError(response.data);
      });
  }, [params.urlTitle]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const edited = await editPage(params.urlTitle, page);
      push(`/wiki/${edited.urlTitle}`);
    } catch ({ response }) {
      setError(response.data);
    }
  };

  const handleChange = ({ target }) => {
    const { value, name } = target;
    setPage({ ...page, [name]: value });
  };

  return (
    <>
      <div className={s.container}>
        <form onSubmit={handleSubmit}>
          <div className={s.header}>
            <h2>EDIT PAGE</h2>
            <FaEdit onClick={handleSubmit} />
          </div>
          <label>
            Page Title
            <input
              onChange={handleChange}
              name="title"
              type="text"
              value={page.title}
              placeholder="Titulo del posteo"
            />
          </label>
          <label>
            Page Content
            <textarea
              onChange={handleChange}
              name="content"
              type="text"
              value={page.content}
              placeholder="Contenido del post, incluye markdown"
            />
          </label>
          <label>
            Tags
            <input
              onChange={handleChange}
              name="tags"
              type="text"
              value={page.tags}
              placeholder="Separar con comas (,)"
            />
          </label>
        </form>
      </div>
      {error && <p className={s.error}>{error}</p>}
    </>
  );
};

export default Form;
