import axios from "axios";

export const search = (value) => {
  return axios.get(`/api/pages/search/${value}`).then((res) => res.data);
};

export const fetchFeed = () => {
  return axios.get(`/api/pages`).then((res) => res.data);
};

export const fetchUsers = () => {
  return axios.get(`/api/users`).then((res) => res.data);
};

export const fetchUserFeed = (id) => {
  return axios.get(`/api/users/${id}`).then((res) => res.data);
};

export const createPage = (page) => {
  return axios.post(`/api/pages`, page).then((res) => res.data);
};

export const fetchPages = (urlTitle) => {
  return axios.get(`/api/pages/${urlTitle}`).then((res) => res.data);
};

export const postDelete = (id) => {
  return axios.delete(`/api/pages/${id}`).then((res) => res.data);
};

export const editPage = (url, page) => {
  return axios.put(`/api/pages/${url}`, page).then((res) => res.data);
};

export const findSimilar = (url) => {
  return axios.get(`/api/pages/${url}/similar`).then((res) => res.data);
};
