import { useEffect, useState } from "react";
import { Link, useHistory, useRouteMatch } from "react-router-dom";

import { FaPen } from "react-icons/fa";
import { FaTrashAlt } from "react-icons/fa";
import { FaUserEdit } from "react-icons/fa";
import { FaBinoculars } from "react-icons/fa";

import { fetchPages, postDelete } from "../../api";
import Loading from "../../components/Loading";

import s from "./Detail.module.scss";

const Detail = () => {
  const { push } = useHistory();
  const { params } = useRouteMatch();
  const [page, setPages] = useState({});

  useEffect(() => {
    fetchPages(params.urlTitle)
      .then((page) => {
        setPages(page);
      })
      .catch(setPages);
  }, [params.urlTitle]);

  const onDelete = async () => {
    await postDelete(page.id);
    push("/wiki");
  };

  if (page instanceof Error) {
    return <h3>Error: Post not found</h3>;
  }

  if (!page.id) return <Loading />;

  return (
    <div className={s.detail}>
      <h2>{page.title}</h2>

      {page.author && (
        <p className={s.author}>
          <span>
            <FaUserEdit /> {page.author.name}
          </span>
          <small>{page.author.email}</small>
        </p>
      )}
      {page.renderedContent ? (
        <div
          className={s.content}
          dangerouslySetInnerHTML={{ __html: page.renderedContent }}
        />
      ) : (
        <div className={s.content}>
          <p>{page.content}</p>
        </div>
      )}
      <div className={s.tagList}>
        {page.tags
          ? page.tags.map((tag) => (
              <Link key={tag} to={`/search/${tag}`}>
                <p className={s.tag}>#{tag}</p>
              </Link>
            ))
          : null}
      </div>
      <div className={s.buttons}>
        <button className={s.delete} onClick={onDelete}>
          <FaTrashAlt />
          delete post
        </button>
        <Link to={`/edit/${page.urlTitle}`}>
          <button className={s.edit}>
            <FaPen />
            edit post
          </button>
        </Link>
        <Link to={`/similar/${page.urlTitle}`}>
          <button>
            <FaBinoculars />
            find similar
          </button>
        </Link>
      </div>
    </div>
  );
};

export default Detail;
